import React from 'react';
import Figure from "./Figure";

function Tab3(props) {
    let mainRef = props.mainRef;
    let runs = mainRef['runs'].filter((item) => {
        return ('figure' in item &&
            (item['run_name'].startsWith('F10') ||
                item['run_name'].startsWith('F11')))
    });
    let runsDict = {};
    runs.forEach((item) => {
        let title = item['figure'];
        if (item['run_name'].startsWith('F10')) {
            item['description'] = "Figure 10: Training success rate of 5 meta- and multi-task " +
                "RL algorithms on the ML45 (RL2-PPO, MAML-TRPO, PEARL-SAC) and MT50 (MT-PPO, MT-SAC) " +
                "benchmarks, and test success rate of the 3 meta-RL algorithms on the ML45 benchmark. " +
                "Mean and 95% confidence interval over 4 random seeds.";
        }

        if (item['run_name'].startsWith('F11')) {
            item['description'] = "Figure 11: Effect of holding out k tasks from MT-PPO on MT10. Mean and 95% " +
                "confidence interval for 5 seeds. All levels of hold-out show similar rise times, although " +
                "holding out 8 tasks impedes performance. Besides 8 held-out, there do not appear to be " +
                "any differences.";
        }

        item['data'] = require('../results/' + item['run_name'] + '.json');
        if (title in runsDict) {
            runsDict[title].push(item);
        }
        else {
            runsDict[title] = [item]
        }
    });

    return (
        <section>
            {Object.keys(runsDict).map((title, idx) => {
                return (
                    <div key={idx}>
                        <Figure runs={runsDict[title]} title={title} />
                        <hr className="hr" />
                    </div>
                )
            })}
        </section>
    );
}

export default Tab3;
