import React from "react";

import 'bulma/css/bulma.min.css'
import './Landing.css'
import Figure from "./Figure";

function Landing(props) {
    let abstract = "Establishing the significance of experimental results in reinforcement learning (RL) " +
        "is difficult. This is compounded by the additional complexity of meta- and multi-task RL, " +
        "a rapidly-growing research area which lacks well-defined baselines. We analyze several " +
        "design decisions each author must make when they implement a meta-RL or MTRL algorithm, " +
        "and show that these seemingly-small details can create variations in a single algorithm's " +
        "performance that exceed the reported performance differences between algorithms themselves. " +
        "Informed by this analysis, we precisely define several important hyperparameters, design " +
        "decisions, and evaluation metrics for meta-RL and MTRL methods, so that we can compare these " +
        "methods reproducibly. We then provide multi-seed benchmark results for seven of the most " +
        "popular meta-RL and MTRL algorithms on the most challenging benchmarks currently available. " +
        "Finally, we share with the community an open source package of these algorithm reference " +
        "implementations, which use our consistent definitions, achieve state-of-the-art-performance, " +
        "and seeks to follow the original works introducing these algorithms as closely as possible.";

    let mainRef = props.mainRef;
    let runs = mainRef['runs'].filter((item) => { return ('figure' in item && item['run_name'].startsWith('F9')) });
    runs.forEach((item) => {
        item['data'] = require('../results/' + item['run_name'] + '.json');
        item['description'] = "Figure 9: Training success rate of 7 meta- and multi-task RL algorithms on the ML45 " +
            "(RL2-PPO, MAML-TRPO, PEARL-SAC) and MT50 (MT-PPO, MT-TRPO, TE-PPO) benchmarks, and" +
            "test success rate of the 3 meta-RL algorithms on the ML45 benchmark. Mean and 95% confidence " +
            "interval over at least 9 random seeds.";
        item['fixy'] = 50;
    });
    let title = runs[0]['figure'];

    return (
        <section className="landing">
            <div className="landing-headline">
                <Figure runs={runs} title={title} />
            </div>
            <div className="landing-description">
                <div className="landing-description-left">
                    <div className="box abstract-box">
                        <h3 className="subtitle has-text-centered abstract-title">Abstract</h3>
                        <p className="abstract-text">{abstract}</p>
                    </div>
                </div>
                <div className="landing-description-right">
                    <div className="links">
                        <button className="button is-rounded is-light is-link link-button">
                            <a href="https://metarl.readthedocs.io/en/latest/">
                                <i className="fas fa-file-alt" />&nbsp;
                                <span>Documentation</span>
                            </a>
                        </button>
                        <button className="button is-rounded is-light is-link link-button">
                            <a href="https://github.com/icml2020submission6857/metarl">
                                <i className="fas fa-file-code" />&nbsp;
                                <span>Code</span>
                            </a>
                        </button>
                        <button className="button is-rounded is-light is-link link-button">
                            <a href="https://drive.google.com/drive/folders/1drr36XLCjHchPzHbQX67z_d0uIdcJ54O">
                                <i className="fab fa-google" />&nbsp;
                                <span>CoLab</span>
                            </a>
                        </button>
                    </div>
                </div>
            </div>
        </section>
    );

}

export default Landing;
