import React from "react";
import Chart from "chart.js"

import './Figure.css'

const colorPattern = [
    'rgb(103, 153, 213)',
    'rgb(217, 122, 73)',
    'rgb(79, 166, 124)',
    'rgb(219, 87, 124)',
    'rgb(140, 100, 201)',
    'rgb(75, 162, 178)',
    'rgb(83, 98, 235)',
    'rgb(176, 70, 154)',
    'rgb(113, 135, 146)',
    'rgb(222, 112, 108)',
    'rgb(60, 100, 147)',
];

const colorPattern_bg = [
    'rgba(103, 153, 213, .3)',
    'rgba(217, 122, 73, .3)',
    'rgba(79, 166, 124, .3)',
    'rgba(219, 87, 124, .3)',
    'rgba(140, 100, 201, .3)',
    'rgba(75, 162, 178, .3)',
    'rgba(83, 98, 235, .3)',
    'rgba(176, 70, 154, .3)',
    'rgba(113, 135, 146, .3)',
    'rgba(222, 112, 108, .3)',
    'rgba(60, 100, 147, .3)',
];

class Figure extends React.Component {
    constructor(props) {
        super(props);
        this.runs = this.props.runs;

        this.fixy = ('fixy' in this.runs[0]) ? this.runs[0].fixy: null;
        this.description = ('description' in this.runs[0]) ? this.runs[0].description: null;
    }

    componentDidMount() {
        this.updateChart();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.updateChart();
    }

    updateChart() {
        if (this.chart) {
            this.chart.destroy();
        }

        let datasets = this.runs.map((run, index) => {
            let indices = [...Array(run['data']['x'].length).keys()];
            let data = indices.map(i => {
                let x = Math.round((run['data']['x'][i] + Number.EPSILON) * 10) / 10;
                let y = Math.round((run['data']['y'][i] + Number.EPSILON) * 10) / 10;
                return {
                    x: x,
                    y: y
                }
            });

            return ({
                index: index,
                label: run['algo'],
                fill: false,
                data: data,
                borderColor: colorPattern[index],
                pointRadius: 0.01,
                pointHoverRadius: 2.4,
                pointBackgroundColor: colorPattern[index],
                backgroundColor: colorPattern_bg[index],
                borderWidth: 1.2,
                showLine: true,
                hidden: (index !== 0)
            });
        });

        let datasets_up = this.runs.map((run, index) => {
            let indices = [...Array(run['data']['x'].length).keys()];
            let data = indices.map(i => { return {
                x: run['data']['x'][i],
                y: run['data']['max'][i],
            } });

            return ({
                index: index,
                label: run['algo']+ 'up',
                fill: index,
                data: data,
                borderColor: 'transparent',
                backgroundColor: colorPattern_bg[index],
                pointRadius: 0,
                pointHoverRadius: 0,
                showLine: true,
                hidden: (index !== 0)
            });
        });

        let datasets_down = this.runs.map((run, index) => {
            let indices = [...Array(run['data']['x'].length).keys()];
            let data = indices.map(i => { return {
                x: run['data']['x'][i],
                y: run['data']['min'][i],
            } });

            return ({
                index: index,
                label: run['algo']+ 'down',
                fill: index,
                data: data,
                borderColor: 'transparent',
                backgroundColor: colorPattern_bg[index],
                pointRadius: 0,
                pointHoverRadius: 0,
                showLine: true,
                hidden: (index !== 0)
            });
        });

        datasets = datasets.concat(datasets_up);
        datasets = datasets.concat(datasets_down);

        this.chart = new Chart(this.myCanvas, {
            type: 'scatter',
            data: {
                datasets: datasets
            },
            options: {
                scales: {
                    xAxes: [{
                        scaleLabel: {
                            display: true,
                            labelString: this.runs[0]['x_name']
                        },
                        ticks: {
                            callback: function(tick) {
                                return tick.toLocaleString();
                            }
                        }
                    }],
                    yAxes: [{
                        ticks: {
                            suggestedMax: this.fixy,
                        },
                        scaleLabel: {
                            display: true,
                            labelString: this.runs[0]['y_name']
                        }
                    }],
                },
                tooltips: {
                    mode: 'x',
                    filter: function (tooltipItem, data) {
                        let L = data.datasets.length / 3;
                        return (tooltipItem.datasetIndex < L);
                    }
                },
                title: {
                    display: true,
                    text: this.props.title
                },
                legend: {
                    labels: {
                        filter: function(item, chart) {
                            return !(item.text.includes('up') || item.text.includes('down'));
                        }
                    },
                    onHover: function(e) {
                        e.target.style.cursor = 'pointer';
                    },
                    onClick: function(e, item) {
                        let is_hidden = item.hidden;
                        datasets.forEach((each) => {
                            if (each.index === item.datasetIndex) {
                                each.hidden = !is_hidden;
                            }
                        });
                        this.chart.update();
                    }
                },
                hover: {
                    onHover: function(e) {
                        let point = this.getElementAtEvent(e);
                        if (point.length) e.target.style.cursor = 'pointer';
                        else e.target.style.cursor = 'default';
                    }
                }
            }
        });
    }

    render() {
        return(
            <div className="canvas-section">
                <canvas ref={node =>{this.myCanvas=node}} id="canvas-chart" />
                <div className="canvas-description">{this.description}</div>
            </div>
        );
    }
}

export default Figure;
