import React, { useState } from 'react';

import 'bulma/css/bulma.min.css'
import './App.css'

import Hero from "./components/Hero";
import Footer from "./components/Footer";
import Landing from "./components/Landing";

import mainRef from './results/figures.json';
import Tab2 from "./components/Tab2";
import Tab3 from "./components/Tab3";
import Tab4 from "./components/Tab4";

function App() {
    const [currentTab, setCurrentTab] = useState(1);
    let lastUpdateTime = mainRef['timestamp'];
    return (
        <div>
            <Hero lastUpdateTime={lastUpdateTime} />
            <br />
            <div className="tabs is-centered">
                <ul>
                    <li className={(currentTab===1) ? "is-active": ""}>
                        <a href="/" key={1} onClick={(e)=> {
                        e.preventDefault();
                        setCurrentTab(1);
                    }}>Main</a></li>
                    <li className={(currentTab===2) ? "is-active": ""}>
                        <a href="/" key={2} onClick={(e)=> {
                            e.preventDefault();
                            setCurrentTab(2);
                        }}>Experiments</a></li>
                    <li className={(currentTab===3) ? "is-active": ""}>
                        <a href="/" key={3} onClick={(e)=> {
                            e.preventDefault();
                            setCurrentTab(3);
                        }}>Additional Experiments</a></li>
                    <li className={(currentTab===4) ? "is-active": ""}>
                        <a href="/" key={4} onClick={(e)=> {
                            e.preventDefault();
                            setCurrentTab(4);
                        }}>Comparison</a></li>
                </ul>
            </div>
            {(currentTab === 1) ? <Landing mainRef={mainRef} /> : null}
            {(currentTab === 2) ? <Tab2 mainRef={mainRef} /> : null}
            {(currentTab === 3) ? <Tab3 mainRef={mainRef} /> : null}
            {(currentTab === 4) ? <Tab4 mainRef={mainRef} /> : null}
            <Footer />
        </div>
    );
}

export default App;
